//IPAD
@media (max-width: 768px) {
    .shadow-block {
        display: none; }
    .main-menu {
        &__logo {
            width: 300px; }
        .modal {
            width: 450px;
            &__text-block {
                font-size: 12px; }
            .button {
                min-width: 31%;
                font-size: 15px; } }
        &__overlay {
            background-repeat: no-repeat;
            background-size: 100%; } }
    .dialog {
        width: 200px; }
    .top-line {
        width: 550px; }
    .progress-bar {
        width: 300px; }
    .devil {
        &__image {

            width: 700px;
            &_menu {
                width: 500px; }
            &_up {
                top: 100px; } } }
    .shadow {
        height: 70%; }
    .game {
        height: 100%;
        width: 100%;
        .container {
            width: 100%;
            height: 100%;
            // background: black
 } }            // background-size:
    .card-list {
        &__block {
            grid-gap: 5px;
            &_simple {
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr;
                width: 500px;
                height: 500px; }
            &_regular {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr;
                width: 550px;
                height: 275px; }
            &_expert {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
                width: 550px;
                height: 400px; } } } }



