//IPHONE_X IPHONE_6/7/8 PLUS
@media only screen and (max-width: 480px) {
    .modal {
        // &_result
        //  // top: 50%
        // top: 50%
        // transform: translate(-50%,-50%)
        // &__result-img
        //     width: 100%
        // width: 90%
