        &__buttons-block {
            // width: 100%
            flex-direction: column;
            .button {
                font-size: 16px;
                margin-bottom: 10px;
                &:last-child {
                    margin-bottom: 0; } } } }
    .devil {
        &__image {
            top: 40%;
            transform: translate(-50%, -50%);
            width: 90%;

            &_up {
                top: 25%; }
            &_menu {
                top: 45%;
                transform: translate(-50%, -50%);
                width: 375px; } } }
    .main-menu {
        &__logo {
            width: 55%;
            top: 15%;
            transform: translate(-50%, -50%); }

        .modal {
            width: 90%;
            top: 65%;
            transform: translate(-50%, -50%);
            padding: 15px;
            &__buttons-block {
                flex-direction: row; }
            .button {
                height: 100%;
                padding: 15px;
                width: 100%;
                font-size: 14px;
                margin-right: 8px;
                &:last-child {
                    margin-right: 0; }
 } }                // height: 100%
        &__overlay {
            background-image: none; } }

    .timer {
        line-height: 1.5;
        left: 50%;
        transform: translateX(-50%); }
    .progress-bar {
        width: 60%; }
    .top-line {
        // flex-direction: column-reverse

        width: 90%;
        bottom: 30px;
        &__buttons {
            .button {
                padding: 15px;
                height: 100%;
                font-size: 12px;
                margin-right: 5px;
                &:last-child {
                    margin-right: 0; } }
